import React from 'react'
import { Form, Input, Button, Select, Radio } from 'antd'
import { MainContext } from '../../context/MainContext'
import { useContext } from 'react'
import { v4 as uuidv4 } from 'uuid'
import { toast, ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { useNavigate } from 'react-router-dom'
import './StudentForm.scss'
function StudentForm({CurrentStudent}) {
  const { Option } = Select
  const { dispatch } = useContext(MainContext)
  let navigate = useNavigate()
  const [form] = Form.useForm()

  //laq zrind dzli olan halda aadak funksiya ilyck
  const Editconnection = (value, id) => {
    value.id = id
    if (JSON.stringify(CurrentStudent) !== JSON.stringify(value)) {
      dispatch({ type: 'Remove_Student', payload: id })
      dispatch({ type: 'Add_Student', payload: value })
      const resolveAfter3Sec = new Promise((resolve) =>
        setTimeout(resolve, 2000),
      )
      toast.promise(resolveAfter3Sec, {
        pending: 'Dyiiklr qeyd alnr',
        success: 'Qeyd alnd, sas Shify Ynlndirilirsiniz ',
      })
      setTimeout(() => {
        navigate('/Students')
      }, 3000)
    } else {
      toast.error('Error! Dyiiklik yoxdur', {
        position: 'top-center',
        autoClose: 1100,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
      })
    }
  }

  // laq lav olunanda bu funksiya ilyck
  const SaveConnection = (value) => {
    value.id = uuidv4()
    dispatch({ type: 'Add_Student', payload: value })
    const resolveAfter3Sec = new Promise((resolve) => setTimeout(resolve, 2000))
    toast.promise(resolveAfter3Sec, {
      pending: 'Tlb lav olunur',
      success: 'lav olundu, sas Shify Ynlndirilirsiniz ',
    })
    setTimeout(() => {
      navigate('/Students')
    }, 3000)
  }

  return (
    <>
      <ToastContainer hideProgressBar position="top-center" />

      <Form
        name="basic"
        form={form}
        labelCol={{
          span: 9,
        }}
        wrapperCol={{
          span: 6,
        }}
        initialValues={
          //gr zrind dyiiklik edilck Tlb varsa , laqnin ilkin datas fieldlr yazilir,
          //yoxdursa (yni ilk df lav olunarsa) default olaraq aadak dyrlr yazlr
          CurrentStudent?
             {
                firstName: CurrentStudent.firstName,
                lastName: CurrentStudent.lastName,
                Address: CurrentStudent.Address,
                pnumber: CurrentStudent.pnumber,
                position: CurrentStudent.position,
                profession: CurrentStudent.profession,
                gender: CurrentStudent.gender,
              }
            : {
                profession:'Ixtisas 1',
                gender: 'Kii',
              }
        }
        autoComplete="off"
        onFinish={(value) => {
          //gr zrind dyiiklik edilck laq varsa , Editconnection funksiyasi laq zrind dyiiklik edir,
          //yoxdursa SaveConnection funkisyasi onu laqlr lav edir
          CurrentStudent?
             Editconnection(value, CurrentStudent.id)
            : SaveConnection(value)

          form.resetFields() // formu sfrlayr
        }}
      >
        <Form.Item
          label="Ad"
          name="firstName"
          rules={[
            {
              required: true,
              message: 'Zhmt olmasa adnz daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Adnz daxil edin" />
        </Form.Item>

        <Form.Item
          label="Soyad"
          name="lastName"
          rules={[
            {
              required: true,
              message: 'Zhmt olmasa soyadnz daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Soyadnz daxil edin" />
        </Form.Item>

        <Form.Item
          label="Address"
          name="Address"
          rules={[
            {
              required: true,
              message: 'Zhmt olmasa addressinizi daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Addressinizi daxil edin" />
        </Form.Item>

        <Form.Item
          label="Phone number"
          name="pnumber"
          rules={[
            {
              required: true,
              message: 'Zhmt olmasa mobil nomrenizi daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Mobil nomrenizi daxil edin" />
        </Form.Item>

        <Form.Item label="Ixtisas" name="profession">
          <Select>
            <Option value="Ixtisas 1">Ixtisas 1</Option>
            <Option value="Ixtisas 2">Ixtisas 2</Option>
            <Option value="Ixtisas 3">Ixtisas 3</Option>
            <Option value="Ixtisas 4">Ixtisas 4</Option>
          </Select>
        </Form.Item>

        <Form.Item label="Cins" name="gender">
          <Radio.Group>
            <Radio value={'Kii'}>Kii</Radio>
            <Radio value={'Qadn'}>Qadn</Radio>
            <Radio value={'Digr'}>Digr</Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item
          wrapperCol={{
            md: {
              offset: 9,
              span: 16,
            },
          }}
        >
          <Button className="Addbtn" htmlType="submit">
            Tsdiql
          </Button>
        </Form.Item>
      </Form>
    </>
  )
}

export default StudentForm;
